import React from 'react'
import styled from 'styled-components'
import pic5 from '../../components/assets/pic5.png'


export const Article2 = () => {
    return (
        <Container>
            <Right>
                <Image src={pic5} alt="sdf"/>
            </Right>
            <Left>
               <SubT>Lorem Ipsum is simply dummy text dummy text</SubT> 
               <Desc>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a gallery of type and scrambled it to make a type specimen book. It has survived not only five centuries.</Desc>
               <Desc>Lorem Ipsum is simply dummy text of the printing and typesetting industry.</Desc>
               <Button>Read more</Button>
            </Left>
        </Container>
    )
}

const Container = styled.section`
    display: flex;
    margin: 20vh 10vw;
    gap: 5vw;
    @media screen and (max-width: 1200px) {
        flex-direction: column;
    }
`
const Left = styled.div`
    flex: 1;
    text-align: left;
`
const SubT = styled.h2`
    line-height: 1.5;
    font-weight: 600;
    font-size: var(--bf);
    margin-bottom: 20px;
`
const Desc = styled.p`
    color: grey;
    font-size: var(--sf);
    margin-bottom: 50px;
`
const Button = styled.button`
    background-color: var(--con);
    font-size: var(--sf);
    padding: 10px 30px;
    cursor: pointer;
    color: white;   
    border: none;
`
const Right = styled.div`
    flex: 1;
`
const Image = styled.img`
    width: 100%;
    max-width: 600px;
`
