import React from 'react'
import {ArrowRight} from '@styled-icons/bootstrap/ArrowRight'
import styled from 'styled-components'
import mobile from "../../components/assets/mobile.svg"
import pc from "../../components/assets/pc.svg"
import arvr from "../../components/assets/arvr.svg"
import vrar from "../../components/assets/vrar.svg"
import threeD from "../../components/assets/3d.svg"
import ps4 from "../../components/assets/ps4.svg"


export const Development = () => {
    const smallCard = (img, title) => (
            <Card>
                <Img src={img} alt="dasd"/>
                <P>{title}</P>
                <ArrowRight/>
            </Card>
    )
    
    
    return (
        <Container>
            <SubT>Lorem Ipsum is simply dummy text of the printing and typesetting industry.</SubT>
            <Desc>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s.</Desc>
            <Wrapper>
                {smallCard(mobile, 'Mobile Game Development')}
                {smallCard(pc, 'PC Game Development')}
                {smallCard(ps4, 'PS4 Game Development')}
                {smallCard(arvr, 'AR/VR Solutions')}
                {smallCard(vrar, 'AR/VR design')}
                {smallCard(threeD, '3D Modelings')}
            </Wrapper>
        </Container>
    )
}

const Container = styled.section`
    min-height: 100vh;
    @media screen and (max-width: 900px) {
        padding: 5vw;
    }
`
const SubT = styled.h2`
    margin: 300px auto 50px;
    max-width: 1200px;
    text-align: center;
    line-height: 2;
    font-size: var(--bf);
    font-weight: 600;
    @media screen and (max-width: 900px) {
        margin: 100px auto 50px;
        line-height: 1.5;
    }
`
const Desc = styled.p`
    color: grey;
    line-height:2;
    max-width: 1200px;
    text-align: center;
    margin: 0 auto;
    font-size: var(--sf);
`
const Wrapper = styled.div`
    display: flex;
    flex-wrap: wrap;
    padding: 5vw;
    justify-content: center;
`
const Card = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 300px;
    justify-content: space-around;
    margin: 30px 50px 0;
    >svg {
        height: 40px;
        color: var(--col);
        cursor: pointer;
    }
    @media screen and (max-width: 700px) {
        justify-content: space-evenly;
        height: 200px;
        >svg {
            height: 20px;
        }
    }
`
const Img = styled.img`
    width: 100px;
    @media screen and (max-width: 700px) {
        width: 60px;
    }
`
const P = styled.p`
    font-size: var(--sf);
    font-weight: 500;
    text-align: center;
`
