import React from 'react'
import styled from 'styled-components'
import map from '../../components/assets/map.png'
import {Facebook} from '@styled-icons/boxicons-logos/Facebook'
import {Instagram} from '@styled-icons/boxicons-logos/Instagram'
import {Twitter} from '@styled-icons/boxicons-logos/Twitter'
import {Linkedin} from '@styled-icons/boxicons-logos/Linkedin'
import {PhoneCall} from '@styled-icons/boxicons-solid/PhoneCall'
import { LocationOn } from '@styled-icons/material'
import { ContactForm } from './ContactForm'

export const Contact = () => {
    return (
        <Container>
            <Path>Home &gt; <span>Contact us</span></Path>
            <SubT>Lorem Ipsum is simply dummy text of the printing and.</SubT>
            <Desc>Lorem Ipsum is simply dummy text of the printing and typesetting industry.</Desc>
            <Image src={map} alt="sfdsd"/>
            <ContactContainer>
                <Wrap>
                    <h3>Follow us</h3>
                    <SideIcon>
                        <Icon><Facebook/></Icon>
                        <Icon><Instagram/></Icon>
                        <Icon><Twitter/></Icon>
                        <Icon><Linkedin/></Icon>
                    </SideIcon>
                </Wrap>
                <Wrap>
                    <PhoneCall/>
                    <p>+94 7477 7738 69</p>
                </Wrap>
                <Wrap>
                    <LocationOn/>
                    <p>but also the leap into electronic typesetting</p>
                </Wrap>
            </ContactContainer>
            <ContactForm />
        </Container>
    )
}


const Container = styled.article`
    text-align: center;
    padding-top: 10vh;
`
const Path = styled.p`
    margin-bottom: 20px;
    >span {
        color: var(--cod);
    }
`
const SubT = styled.h2`
    line-height: 2;
    font-weight: 600;
    font-size: var(--bf);
    margin-bottom: 20px;
`
const Desc = styled.p`
    line-height: 2;
    font-weight: 400;
    font-size: var(--xsf);
    color: grey;
    margin-bottom: 3rem;
`
const Image = styled.img`
    width: 80%;
`
const ContactContainer = styled.section`
    display: flex;
    text-align: left;
    justify-content: space-between;
    margin: 50px auto 150px;
    @media screen and (max-width: 1000px) {
        flex-direction: column;
        gap: 30px;
    }
`
const Wrap = styled.div`
    border-right: 1px solid lightgray;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
    font-size: var(--sf);
    padding: 0 5vw;
    &:nth-child(1) {
        >h3 {
            font-weight: 400;
            width: 250px;
        }
        flex-direction: column;
        flex: 1;
    }
    &:nth-child(2) {
        flex: 1;
        >svg {
            height: 32px;
        }
    }
    &:nth-child(3) {
        flex: 1.5;
        border: none;
        white-space: wrap;
        >svg {
            height: 32px;
        }
    }
    @media screen and (max-width: 1000px) {
        border: none;
        gap: 10px; 
    }
`
const SideIcon = styled.div`
    display: flex;
    gap: 20px;
    width: 250px;
`
const Icon = styled.div`
    width: 40px;
    height: 40px;
    background-color: white;
    border-radius: 50%;
    display: grid;
    place-content: center;
    cursor: pointer;
    transition: all .3s ease;
    box-shadow: 0 10px 10px whitesmoke;
    >svg {
        color: black;
    }
    &:hover {
        background-color: #585858;
        >svg {
            color: white;
        }
    }
` 