import React,{useState} from 'react'
import styled from 'styled-components'
import pic1 from '../../components/assets/pic1.png'
import pic2 from '../../components/assets/pic2.jpg'
import {ArrowRight} from "@styled-icons/bootstrap/ArrowRight"
import {ArrowLeft} from "@styled-icons/bootstrap/ArrowLeft"
import { JoinUs } from './JoinUs'
import { Description } from './Description'
import { OurTeam } from './OurTeam'
import { Partners } from './Partners'

export const About = () => {
    const [slide, setSlide] = useState(true)
    return (
        <Container>
            <Wrapper>
                <Left>
                    <Path>Home &gt; <span>About us</span></Path>
                    <SubT>Lorem Ipsum is simply dummy text of the printing and.</SubT>
                    <Desc>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s</Desc>
                    <Button>Get in touch →</Button>
                </Left>
                <Right>
                    <Img1 slide={slide} src={pic1} alt="fsdfsd" />
                    <Img2 slide={slide} src={pic2} alt="fsdfs" />
                    <BtnsWrap>
                        <Cont onClick={() => setSlide(true)} ><ArrowLeft/></Cont>
                        <Cont2>{slide ? "1" : "2"} of 2</Cont2>
                        <Cont onClick={() => setSlide(false)} ><ArrowRight/></Cont>
                    </BtnsWrap>
                </Right>
            </Wrapper>
            <JoinUs/>
            <Description/>
            <OurTeam/>
            <Partners/>
        </Container>
    )
}


const Container = styled.article`

`
const Wrapper = styled.section`
    height: calc(100vh - 100px);
    display: flex;
    @media screen and (max-width: 1200px) {
        flex-direction: column-reverse;
    }
`
const Left = styled.div`
    width: 50vw;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 10vw;
    background-color: #FCEFE2;
    align-items: flex-start;
    @media screen and (max-width: 1200px) {
        height: 50vh;
        width: auto;
        padding: 5vw;
    }
`
const Path = styled.p`
    margin-bottom: 50px;
    >span {
        color: var(--cod);
    }
    @media screen and (max-width: 1200px) {
        margin-bottom: 20px;
    }
`
const SubT = styled.h2`
    line-height: 2;
    font-weight: 600;
    font-size: var(--bf);
    margin-bottom: 50px;
    @media screen and (max-width: 1200px) {
        line-height: 1.5;
        margin-bottom: 20px;
    }
`
const Desc = styled.p`
    line-height: 2;
    font-weight: 400;
    font-size: var(--xsf);
    color: grey;
    margin-bottom: 3rem;
    @media screen and (max-width: 1200px) {
        margin-bottom: 2rem;;
    }
`
const Button = styled.button`
    color: white;
    border: none;
    padding: 15px 30px;
    background-color: var(--col);
    border-radius: 30px;
    font-size: var(--xsf);
    cursor: pointer;
    margin-bottom: 3rem;
`
const Right = styled.div`
    width: 50vw;
    position: relative;
    @media screen and (max-width: 1200px) {
        height: 50vh;
        width: auto;
    }
`
const Img1 = styled.img`
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    inset: 0;
    transition: opacity .5s ease;
    opacity: ${props => props.slide ? 1 : 0};
`
const Img2 = styled.img`
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    inset: 0;
    transition: opacity .5s ease;
    opacity: ${props => props.slide ? 0 : 1};
`
const BtnsWrap = styled.div`
    position: absolute;
    width: 300px;
    height: 100px;
    bottom: 0;
    left: 0;
    color: white;
    background-color: #3a3a3a;
    display: flex;
    @media screen and (max-width: 600px) {
        width: 150px;
        height: 50px;
    }
`
const Cont = styled.div`
    display: grid;
    place-content: center;
    flex: 1;
    cursor: pointer;
    &:hover {
        background-color: #202020;
    }
`
const Cont2 = styled.div`
    display: grid;
    place-content: center;
    flex: 1;
`
