import React from 'react'
import styled from 'styled-components'
import team1 from '../../components/assets/team1.png'
import team2 from '../../components/assets/team2.png'
import team3 from '../../components/assets/team3.png'
import team4 from '../../components/assets/team4.png'


export const OurTeam = () => {
    const smallCard = (img, name, pos) => (
        <Card>
            <Img src={img} alt="dfsd"/>
            <Name>{name}</Name>
            <Pos>{pos}</Pos>
        </Card>
    )
    return (
        <Container>
            <SubT>Our Team</SubT>
            <Wrapper>
                {smallCard(team1, "Josh Fluke", "CEO")}
                {smallCard(team2, "Ana-Maria", "Assist")}
                {smallCard(team3, "Van Basten", "Analyst")}
                {smallCard(team4, "Mario Celeb", "Dev")}
            </Wrapper>
        </Container>
    )
}

const Container = styled.section`
    padding: 10vh 5vw;
`
const SubT = styled.h2`
    font-size: var(--bf);
    margin-bottom: 2rem;
    font-weight: 600;
`
const Wrapper = styled.div`
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
`
const Card = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: whitesmoke;
    height: 300px;
    width: 300px;
    border-radius: 20px;
`
const Img = styled.img`

`
const Name = styled.p`
    margin: 10px;
    font-weight: 500;
`
const Pos = styled.p`
    color: grey;
`