import React from 'react'
import styled from 'styled-components'
import {CheckCircleFill} from '@styled-icons/bootstrap/CheckCircleFill'
import vid1 from '../../components/assets/vid1.mp4'

export const VideoSection = () => {
    return (
        <Container>
            <Left>
                <Video src={vid1} 
                    type="video/mp4" 
                    controls={false}
                    muted={false}
                    loop 
                    onClick={(e) => e.target.paused ? e.target.play() : e.target.pause()}
                >
                </Video>
            </Left>
            <Right>
                <SubT>Lorem Ipsum is simply dummy text.</SubT>
                <Desc>Lorem ipsum is simply dummy text of the printing and typesetting industry. Lorem ipsum is simply dummy.</Desc>
                <Ul>
                    <Li><CheckCircleFill/> Lorem Ipsum is simply</Li>
                    <Li><CheckCircleFill/> Lorem Ipsum is simply</Li>
                    <Li><CheckCircleFill/> Lorem Ipsum is simply</Li>
                    <Li><CheckCircleFill/> Lorem Ipsum is simply</Li>
                </Ul>
            </Right>
        </Container>
    )
}

const Container = styled.section`
    display: flex;
    margin: 20vh 10vw 0;
    gap: 5vw;
    @media screen and (max-width: 1000px) {
        flex-direction: column;
    }
`
const Left = styled.div`
    flex: 1;
    display: grid;
    place-content: center;
`
const Video = styled.video`
    width: 100%;
`
const Right= styled.div`
    flex: 1;
    text-align: left;
`
const SubT = styled.h2`
    line-height: 1.5;
    font-weight: 600;
    font-size: var(--bf);
    margin-bottom: 20px;
`
const Desc = styled.p`
    line-height: 2;
    font-weight: 400;
    font-size: var(--xsf);
    color: grey;
    margin-bottom: 20px;
`
const Ul = styled.ul`

`
const Li = styled.li`
    line-height: 2;
    color: grey;
    list-style: none;
    font-size: var(--xsf);
    >svg {
        color: green;
        margin-right: 10px;
    }
`
