import React from 'react'
import styled from 'styled-components'

export const JoinUs = () => {
    const smallCard = (color) => (
        <Card>
            <CardSubT color={color}>Lorem Ipsum</CardSubT>
            <CardTitle>Lorem Ipsum</CardTitle>
            <CardDesc>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s</CardDesc>
        </Card>
    )
    
    
    return (
        <Container>
            <SubT>Why work with us</SubT>
            <Wrapper>
                {smallCard("#ff00ea")}
                {smallCard("#ff0000")}
                {smallCard("#3cff00")}
            </Wrapper>
        </Container>
    )
}

const Container = styled.section`
    padding: 10vh 5vw;
`
const SubT = styled.h2`
    font-size: var(--bf);
    margin-bottom: 2rem;
    font-weight: 600;
`
const Wrapper = styled.div`
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    gap: 40px;
`
const Card = styled.article`
    box-sizing: border-box;
    height: 400px;
    width: 450px;
    padding: 40px;
    background-color: whitesmoke;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    transition: all .3s ease;
    &:hover {
        background-color: white;
        box-shadow: 0 0 70px 1px lightgray;
    }
    @media screen and (max-width: 500px) {
        width: 330px;
    }
`
const CardSubT = styled.h3`
    color: ${props => props.color};
    font-weight: 400;
    font-size: var(--xsf);
    background-color: ${props => props.color + "20"};
    padding: 10px 20px;
    border-radius: 10px;
    align-self: flex-start;
`
const CardTitle = styled.h2`

`
const CardDesc = styled.p`
    line-height: 1.7;
    color: #636363;
`