import React from 'react'
import styled from 'styled-components'
import sponsor1 from '../../components/assets/sponsor1.png'
import sponsor2 from '../../components/assets/sponsor2.png'
import sponsor3 from '../../components/assets/sponsor3.png'
import sponsor4 from '../../components/assets/sponsor4.png'
import sponsor5 from '../../components/assets/sponsor5.png'


export const Partners = () => {
    return (
        <Container>
            <SubT>Our Partnership and Collaborators</SubT>
            <Wrapper>
                <Img src={sponsor1} alt="dfsdf"/>
                <Img src={sponsor2} alt="dfsdf"/>
                <Img src={sponsor3} alt="dfsdf"/>
                <Img src={sponsor4} alt="dfsdf"/>
                <Img src={sponsor5} alt="dfsdf"/>
            </Wrapper>
        </Container>
    )
}

const Container = styled.section`
    padding: 10vh 5vw;
    background-color: whitesmoke;
`
const SubT = styled.h2`
    font-size: var(--bf);
    margin-bottom: 2rem;
    font-weight: 600;
`
const Wrapper = styled.div`
    display: flex;
    flex-wrap: wrap;
    column-gap: 100px;
    row-gap: 20px;
`
const Img = styled.img`
    object-fit: contain;
`
