import React from 'react'
import styled from 'styled-components'
import pic3 from "../../components/assets/pic3.png"

export const Description = () => {
    return (
        <Container>
            <Wrapper>
                <Left></Left>
                <Right>
                    <SmallText>Lorem ipsum</SmallText>
                    <SubT>Lorem Ipsum is imply dummy text of the printing and typesetting industry.</SubT>
                    <Desc>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a gallery of type and scrambled it to make a type specimen book. It has survived not only five centuries.</Desc>
                </Right>
            </Wrapper>
        </Container>
    )
}

const Container = styled.section`
    background-color: whitesmoke;
    padding: 10vh 10vw 0;
`
const Wrapper = styled.div`
    display: flex;
    gap: 5vw;
    @media screen and (max-width: 1000px){
        flex-direction: column;
    }
`
const Left = styled.div`
    flex: 1;
    background: url(${pic3}) center/cover;
    min-height: 70vh;
    @media screen and (max-width: 1000px){
        min-height: 30vh;
    }
`
const Right = styled.div`
    flex: 1;
`
const SmallText = styled.small`
    font-size: var(--xsf);
    color: var(--col);
    margin-bottom: 50px;
    display: block;
`
const SubT = styled.h2`
    font-size: var(--bf);
    margin-bottom: 3rem;
    font-weight: 600;
`
const Desc = styled.p`
    line-height: 2;
    font-weight: 400;
    font-size: var(--xsf);
    /* color: grey; */
    margin-bottom: 3rem;
`
