import styled from "styled-components"
import {NavLink} from "react-router-dom"
import { Phone } from '@styled-icons/icomoon/Phone'
import { LocationOn } from '@styled-icons/material'
import {useEffect, useState} from "react"


export const Navbar = () => {
    const [menu, setMenu] = useState(false)
    const handleClick = () => {
        setMenu(!menu);
    }
    useEffect(() => {
        function switchOpen() {
            if (window.innerWidth > 900) {
                setMenu(false)
            }
        }
        window.addEventListener("resize", switchOpen)
        return () => window.removeEventListener("resize", switchOpen)
    }, [setMenu])
    
    return (
        <Container>
            <TopSide>
                <Info>
                    <LocationOn/>
                    Blvd. Eminescu, 304469
                </Info>
                <Info>
                    <Phone/>
                    +94 7477 7733 98
                </Info>
            </TopSide>
            <BottomSide  className={menu && "active"}>
                <Logo>LOGO</Logo>
                <LinksWrapper>
                    <MyNavLink to="/" exact={true} activeClassName="active">Home</MyNavLink>
                    <MyNavLink to="/about" activeClassName="active">About us</MyNavLink>
                    <MyNavLink to="/services" activeClassName="active">Services</MyNavLink>
                    <MyNavLink to="/news" activeClassName="active">News</MyNavLink>
                    <MyNavLink to="/contact" className="contact" activeClassName="active">Contact us</MyNavLink>
                </LinksWrapper>
                <Menu>
                    <Hamburger onClick={handleClick}>
                        <Line1/>
                        <Line2/>
                        <Line3/>
                    </Hamburger>
                </Menu>
            </BottomSide>

            <MobileMenu className={menu && "active"}>
                <Ul>
                    <Li onClick={handleClick}>
                        <MyLink to="/">Home</MyLink>
                    </Li>
                    <Li onClick={handleClick}>
                        <MyLink to="/about">About us</MyLink>
                    </Li>
                    <Li onClick={handleClick}>
                        <MyLink to="/services">Services</MyLink>
                    </Li>
                    <Li onClick={handleClick}>
                        <MyLink to="/news">News</MyLink>
                    </Li>
                    <Li onClick={handleClick}>
                        <MyLink to="/contact">Contact</MyLink>
                    </Li>
                </Ul>
            </MobileMenu>
        </Container>
    )
}

const Container = styled.nav`
    position: relative;
    z-index: 69;
`
const TopSide = styled.div`
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    font-size: var(--xsf);
   `
const Info = styled.small`
    display: flex;
    align-items: center;
    margin-right: 100px;
    @media screen and (max-width: 500px) {
        margin-right: 20px;
    }
`
const Line1 = styled.span`

`
const Line2 = styled.span`

`
const Line3 = styled.span`

`
const BottomSide = styled.div`
    background-color: var(--con);
    display: flex;
    height: 70px;
    justify-content: space-between;
    align-items: center;
    padding: 0 100px;
    transition: all .5s ease;
    &.active {
        background-color: white;
        >h2 {
            color: var(--col);
        }
        ${Line1} {
            /* background-color: var(--col); */
            transform: rotate(45deg);
        }
        ${Line2} {
            opacity: 0;
        }
        ${Line3} {
            /* background-color: var(--col); */
            transform: rotate(-45deg);
        }
    }
    @media screen and (max-width: 900px) {
        padding: 0 5vw;
    }
`
const Logo = styled.h2`
    color: white;
`
const LinksWrapper = styled.div`
    display: flex;
    align-items: center;
    gap: 5vw;
    @media screen and (max-width: 900px) {
        display: none;
    }
`
const MyNavLink = styled(NavLink)`
    color: white;
    text-decoration: none;
    &.contact {
        color: var(--col);
        background-color: white;
        border-radius: 5px;
        padding: 10px 30px;
        font-size: 0.9rem;
    }
    &.active {
        position: relative;
        &::after {
            position: absolute;
            content: "●";
            left: -20px;
            color: white;
            margin: auto 0;
        }
    }
`
const Menu = styled.div`
    display: none;
    display: relative;
    z-index: 70;
    @media screen and (max-width: 900px) {
        display: block;
    }
`
const Hamburger = styled.div`
    width: 32px;
    height: 35px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    cursor: pointer;
    >span {
        width: 100%;
        height: 3px;
        background-color: white;
        transform-origin: left;
        transition: all 1s ease;
    }
`
const MobileMenu = styled.aside`
    width: 300px;
    height: 100vh;
    background-color: var(--col);
    position: fixed;
    top: 0;
    right: -300px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: right 1s ease;
    &.active {
        right: 0;
    }
    @media screen and (min-width: 900px) {
        display: none;
    }
`
const Ul = styled.ul`
    list-style: none;
    font-size: 2rem;
    font-weight: 200;
    width: 60%;
`
const Li = styled.li`
    margin-bottom: 20px;
`
const MyLink = styled(NavLink)`
    text-decoration: none;
    color: white;
    &:hover {
        font-weight: 500;
    }
`

