import React, { useState } from 'react'
import {ArrowIosForwardOutline} from '@styled-icons/evaicons-outline/ArrowIosForwardOutline'
import {ArrowIosBackOutline} from '@styled-icons/evaicons-outline/ArrowIosBackOutline'
import styled from 'styled-components'
import {StarFill} from '@styled-icons/bootstrap/StarFill'
import avatar1 from '../../components/assets/avatar1.png'
import avatar2 from '../../components/assets/avatar2.png'
import avatar3 from '../../components/assets/avatar3.png'


export const EndSection = () => {
    const [slide, setSlide] = useState(0)
    
    const handleClick = (dir) => {
        if (dir === "left") {
            setSlide(slide > 0 ? slide - 1 : 3)
        } else {
            setSlide(slide < 3 ? slide + 1 : 0)
        }
    }

    const smallCard = (avatar, name, location, rating, desc, i) => (
        <Card className={slide === i ? "active" : ""}>
            <Top>
                <Left>
                    <Avatar src={avatar} alt="dfsdsd"/>
                    <Wrap>
                        <Name>{name}</Name>
                        <Location>{location}</Location>
                    </Wrap>
                </Left>
                <Right>
                    <Rating>{rating}</Rating>
                    <StarFill/>
                </Right>
            </Top>
            <CardDesc>{desc}</CardDesc>
        </Card>
    )
    
    return (
        <Container>
            <SubT>Trusted by Thousands of Happy Customer</SubT>
            <Desc>Lorem ipsum is simply dummy text of the printing and typesetting industry. Lorem ipsum is simply dummy.</Desc>
            <Wrapper>
                {smallCard(avatar1, "Viezh Robert", "Warsaw, Poland", "4.5", "Lorem ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s.", 0)}
                {smallCard(avatar2, "Yessica Christy", "Shanxi, China", "4.8", "Lorem Ipsum is simply dummy text of the printing and", 1)}
                {smallCard(avatar3, "Kim Young Jou", "Seoul, South Korea", "2.1", "Lorem Ipsum is simply dummy text of the printing and typesetting industry.", 2)}
                {smallCard(avatar1, "Fred Marquez", "Sofia, Bulgaria", "3.3", "Lorem Ipsum is simply dummy text of the printing and typesetting industry.", 3)}
            </Wrapper>
            <Bottom>
                <Dots>
                    <Dot/>
                    <Dot/>
                    <Dot/>
                    <Dot/>
                    <Dot/>
                    <Line/>
                </Dots>
                <Arrows>
                    <Button onClick={() => handleClick("left")}><ArrowIosBackOutline/></Button>
                    <Button onClick={() => handleClick("right")}><ArrowIosForwardOutline/></Button>
                </Arrows>
            </Bottom>
        </Container>
    )
}


const Container = styled.section`
    background-color: whitesmoke;
    padding: 10vh 5vw;
`
const SubT = styled.h2`
    line-height: 1.5;
    font-weight: 600;
    font-size: var(--bf);
    margin: 0 auto 20px;
    max-width: 600px;
`       
const Desc = styled.small`
    color: grey;
    font-size: var(--xsf);
    margin: 0 auto 100px;
    max-width: 700px;
    display: block;
`
const Wrapper = styled.div`
    display: flex;
    gap: 50px;
    padding-bottom: 100px;
    width: 2200px;
    overflow: hidden;
    transition: transform .5s ease-in-out;
    transform: translateX(${props => props.slide * -490}px);
    @media screen and (max-width: 500px) {
        transform: translateX(${props => props.slide * -370}px);
    }
`
const Card = styled.div`
    width: 450px;
    border: 2px solid lightgray;
    border-radius: 10px;
    padding: 30px;
    &.active {
        background-color: white;
        border: 2px solid var(--cod);
    }
`
const Top = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
`
const Left = styled.div`
    display: flex;
    gap: 20px;
`
const Avatar = styled.img`

`
const Wrap = styled.div`
    text-align: left;
`
const Name = styled.p`
    font-weight: 700;
`
const Location = styled.p`

`
const Right = styled.div`
    display: flex;
    align-items: baseline;
    gap: 10px;
    >svg {
        color: var(--cod);
    }
`
const Rating = styled.p`

`
const CardDesc = styled.p`
     color: grey;
    font-size: var(--xsf);
    text-align: left;
`
const Bottom = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
`
const Dots = styled.div`
    width: 180px;
    display: flex;
    justify-content: space-between;
    position: relative;
`
const Dot = styled.div`
    height: 20px;
    width: 20px;
    border-radius: 50%;
    background-color: lightgray;
`
const Line = styled.div`
    position: absolute;
    z-index: 1;
    border-radius: 25px;
    width: 60px;
    height: 20px;
    background-color: var(--cod);
    /* opacity: 0.1; */
`
const Arrows = styled.div`

`
const Button = styled.button`
    border: 2px solid var(--cod);
    border-radius: 50%;
    margin: 0 10px;
    width: 50px;
    height: 50px;
    cursor: pointer;
    >svg {
        height: 30px;
        color: var(--cod);
    }
    &:hover {
        background-color: var(--cod);
        >svg {
            color: white;
        }
    }
`