import styled from 'styled-components'
import icon1 from '../../components/assets/icon1.png'
import icon2 from '../../components/assets/icon2.png'
import icon3 from '../../components/assets/icon3.png'
import { Article1 } from './Article1'
import { Article2 } from './Article2'
import { Article3 } from './Article3'
import {CardIncrement} from "./CardIncrement"
import { EndSection } from './EndSection'
import { VideoSection } from './VideoSection'


export const Services = () => {
    return (
        <Container>
            <Path>Home &gt; <span>Services</span></Path>
            <SubT>Lorem Ipsum is simply dummy text of the printing and.</SubT>
            <Desc>Lorem Ipsum is simply dummy text of the printing and typesetting industry.</Desc>
            <Wrapper>
                <CardIncrement icon={icon1} max={90}  text="Clients"/>
                <CardIncrement icon={icon2} max={30} text="Countries" />
                <CardIncrement icon={icon3} max={50} text="Projects" />
            </Wrapper>
            <VideoSection/>
            <Article1/>
            <Article2/>
            <Article3/>
            <EndSection/>
        </Container>
    )
}


const Container = styled.article`
    text-align: center;
    padding-top: 10vh;
`
const Path = styled.p`
    margin-bottom: 20px;
    >span {
        color: var(--cod);
    }
`
const SubT = styled.h2`
    line-height: 2;
    font-weight: 600;
    font-size: var(--bf);
    margin-bottom: 20px;
`
const Desc = styled.p`
    line-height: 2;
    font-weight: 400;
    font-size: var(--xsf);
    color: grey;
    margin-bottom: 3rem;
`
const Wrapper = styled.div`
    margin: 5vh 10vw;
    display: flex;
    padding: 40px 0;
    box-shadow: 0 20px 50px 10px whitesmoke;
`
