import React from 'react'
import styled from 'styled-components'
import {Search} from '@styled-icons/bootstrap/Search'
import img1 from '../../components/assets/feed1.png'
import img2 from '../../components/assets/feed2.png'
import img3 from '../../components/assets/feed3.png'
import img4 from '../../components/assets/feed4.png'
import img5 from '../../components/assets/feed5.png'
import img6 from '../../components/assets/feed6.png'
import img7 from '../../components/assets/feed7.png'
import mainImg from '../../components/assets/feed-main.png'
import secondImg from '../../components/assets/feed-second.png'

export const News = () => {
    const article = (color, img) => (
        <Article>
            <Image src={img} alt="fdfd"/>
            <RightWrap>
                <Link color={color}>John Smash</Link>
                <span>.5min</span>
                <ArticleTitle>Lorem Ipsum is simply dummy text dummy text?</ArticleTitle>
            </RightWrap>
        </Article>
    )
    
    
    return (
        <Container>
            <Path>Home &gt; <span>News</span></Path>
            <SubT>Lorem Ipsum is simply dummy text of the printing and.</SubT>
            <Desc>Lorem Ipsum is simply dummy text of the printing and typesetting industry.</Desc>
            <SearchWrap>
                <Search/>
                <Input type="text" placeholder="Search"/>
            </SearchWrap>
            <Feed>
                <Left>
                    <ArticleImage src={mainImg} alt="dsdfd"/>
                    <Link color="#001aff">John Smash</Link>
                    <span>.5min</span>
                    <ArticleTitle>Lorem Ipsum is simply dummy text dummy text?</ArticleTitle>
                    <ArticleDesc>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a gallery of type and scrambled it to make a type specimen book.</ArticleDesc>
                    <ArticleDesc>Lorem Ipsum is simply dummy text of the printing and typesetting industry.</ArticleDesc>
                    <Hr/>
                    <ArticleImage src={secondImg} alt="dsdfd"/>
                    <Link color="#001aff">John Smash</Link>
                    <span>.5min</span>
                    <ArticleTitle>Lorem Ipsum is simply dummy text dummy text?</ArticleTitle>
                    <ArticleDesc>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a gallery of type and scrambled it to make a type specimen book.</ArticleDesc>
                </Left>
                <Right>
                    {article("#000000", img1)}
                    {article("#001aff", img2)}
                    {article("#ff7b00", img3)}
                    {article("#001aff", img4)}
                    {article("#3cff00", img5)}
                    {article("#001aff", img6)}
                    {article("#ff00ea", img7)}
                </Right>
            </Feed>
        </Container>
    )
}


const Container = styled.article`
    text-align: center;
    padding-top: 10vh;
`
const Path = styled.p`
    margin-bottom: 20px;
    >span {
        color: var(--cod);
    }
`
const SubT = styled.h2`
    line-height: 2;
    font-weight: 600;
    font-size: var(--bf);
    margin-bottom: 20px;
`
const Desc = styled.p`
    line-height: 2;
    font-weight: 400;
    font-size: var(--xsf);
    color: grey;
    margin-bottom: 3rem;
`
const SearchWrap = styled.div`
    background-color: whitesmoke;
    margin: 50px auto 20px;
    display: inline-block;
    padding: 10px 20px;
    color: grey;
    border-radius: 10px;
    width: 350px;
    max-width: 90vw;
    text-align: left;
`
const Input = styled.input`
    background-color: transparent;
    border: none;
    outline: none;
    padding-left: 10px;
`
const Feed = styled.article`
    padding: 5vh 10vw 0;
    display: flex;
    gap: 50px;
    text-align: left;
    margin-top: 50px;
`
const Left = styled.div`
    flex: 1;
`
const ArticleImage = styled.img`
    width: 100%;
    margin-bottom: 20px;
`
const ArticleDesc = styled.p`
    margin: 20px 0;
    line-height: 2;
`
const Hr = styled.hr`
    border: 1px solid whitesmoke;
    margin: 70px 0;
`
const Right = styled.div`
    display: flex;
    flex-direction: column;
    gap: 50px;
    flex: 1;
`
const Article = styled.section`
    display: flex;
    gap: 20px;
    align-items: center;
`
const Image = styled.img`
    height: 150px;
`
const RightWrap = styled.div`

`
const Link = styled.a`
    background-color: ${props => props.color};
    color: white;
    padding: 5px 20px;
    cursor: pointer;
    margin-right: 20px;
`
const ArticleTitle = styled.h2`
    margin-top: 30px;
    font-size: var(--sf);
`