import React from 'react'
import styled from 'styled-components'
import vect1 from "../../components/assets/vect1.png"
import vect2 from "../../components/assets/vect2.png"


export const ContactForm = () => {
    return (
        <Container>
            <Title>Say hello</Title>
            <Desc>Lorem ipsum is simply dummy text of the printing.</Desc>
            <Form>
                <Wrap>
                    <Label>
                        First Name
                        <Input type="text" />
                    </Label>
                    <Label>
                        Last Name
                        <Input type="text" />
                    </Label>
                </Wrap>
                <Label>
                    Email Address
                    <Input type="email" />
                </Label>
                <Label>
                    Message
                    <Textarea resize={false}></Textarea>
                </Label>
                <Button>Get in touch</Button>
            </Form>
            <Vect1 src={vect1} alt="dfsdf"/>
            <Vect2 src={vect2} alt="dfsdf"/>
        </Container>
    )
}

const Container = styled.section`
    min-height: 100vh;
    padding: 5vh 5vw;
    background-color: whitesmoke;
    position: relative;
`
const Title = styled.h1`
    font-size: var(--bf);
    font-weight: 400;
    margin-bottom: 1rem;
    `
const Desc = styled.p`
    color: grey;
    text-align: center;
    margin: 0 auto;
    font-size: var(--xsf);
    `
const Form = styled.form`
    position: relative;
    z-index: 1;
    width: 1000px;
    max-width: calc(100vw - 10vw);
    display: flex;
    flex-direction: column;
    text-align: left;
    margin: 100px auto;
    gap: 20px;
    `
const Wrap = styled.div`
    display: flex;
    gap: 20px;
    width: 100%;
    @media screen and (max-width: 800px) {
        flex-direction: column;
    }
`
const Label = styled.label`
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 10px;
    &:nth-child(3), &:nth-child(4) {
        width: 100%;
    }
`
const Input = styled.input`
    border: 1px solid lightgray;
    border-radius: 10px;
    font-size: 1.5rem;
    padding: 10px 15px;
    `
const Textarea = styled.textarea`
    font-size: 1.5rem;
    padding: 10px 15px;
    border-radius: 10px;
    border: 1px solid lightgray;
    height: 30vh;
    resize: none;
`
const Button = styled.button`
    background-color: var(--con);
    font-size: var(--xsf);
    padding: 15px 40px;
    cursor: pointer;
    color: white;   
    border: none;
    border-radius: 10px;
    align-self: flex-end;
    margin-top: 30px;
`
const Vect1 = styled.img`
    position: absolute;
    top: 0;
    right: 0;
    width: 75%;
    z-index: 0;
    `
const Vect2 = styled.img`
    position: absolute;
    bottom: 0;
    left: 0;
    width: 45%;
    z-index: 0;

`